main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}

:root {
  --primary: #02080c;
  --secondary: #F75C03;
  --tertiary: #F1C40F;
  --fourth: #D90368;
  --fifth: #00CC66;
  --light: #04507c;
  --lighter: #2a9adb;
  --dark: #01020a;
  --darker: #070300;
  --error: tomato;
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
  --header-img: url(./assets/photos/rainbowbrick.jpg);
  --footer-img: url(./assets/photos/rainbowbrick.jpg);
  --flipcard-img: url(./assets/photos/rainbowbrick-flipcard.jpg);
}

* {
  box-sizing: border-box;
}

html, body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: var(--darker);
  color: var(--lighter);
  font-family: 'Nexa Demo', bold;
}

p {
  font-size: 1.1rem;
  margin: 10px;
  border: 5px;
  padding: 25px;
  justify-content: center;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

h3 {
  text-align: center;
}

a:hover {
  text-decoration: underline;
  color: var(--fifth);
}

.header {
  background: var(--header-img);
}

header a {
  color: var(--dark);
  padding: 7px 15px;
  margin-left: 2px;
  margin-right: 2px;
  font-size: 30px;
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: 0.8;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

.bio-img {
  max-width: 40%;
  height: fit-content;
}

img {
  max-width: 70%;
  padding-top: 28px;
  padding-bottom: 8px;
}

form {
  display: flex;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: underline;
  color: var(--tertiary)
}

nav a:active {
  text-decoration: underline;
  color: var(--fourth);
}

nav li {
  cursor: pointer;
}

textarea {
  max-width: 600px;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}

.download h2 a {
  color: var(--tertiary);
}

.download h2 {
  color: var(--fifth);
}

.margin {
  margin: 0 auto;
  margin-top: 1em;
}

.container {
  margin: 10px 5px 10px 5px;
  padding: 5px;
  width: 100%;
  max-width: 1200px;
}

.flex-row {
  display: flex;
}

.flex-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.space-between {
  justify-content: space-between;
}

.space-around {
  justify-content: space-around;
}

.flex-end {
  justify-content: flex-end;
}

#contactButton {
  text-align: center;
}

#flip-card {
  background-color: transparent;
  width: 300px;
  height: 400px;
  perspective: 1000px;
}

#flip-data {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.7s;
  transform-style: preserve-3d;
  box-shadow: 2px 8px 12px 2px rgba(0, 0, 0, 0.2);
}

#flip-card:hover #flip-data {
  transform: rotateY(180deg);
}

#flip-front, #flip-back {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 5px;
  border: 2px solid rgb(8, 8, 8);
  border-radius: 20px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

#flip-front {
  background: var(--flipcard-img);
  color: var(--darker);
}

#flip-back {
  background: var(--flipcard-img);
  color: var(--darker);
  transform: rotateY(180deg);
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-name {
  padding: 3px;
}

.project-description {
  padding: 3px;
}

.card {
  /* width: 25%; */
  text-align: center;
}

