#navActive {
  color: var(--secondary);
}

footer {
  position: sticky;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 0;
  background-image: var(--footer-img);
}

#footerLink {
  color: #01020a;
  font-style: bold;
  padding: 2px 7px 2px 7px;
}